body {
  text-align: center;

  background-color: rgb(30, 30, 108);
}

* {
  font-weight: 700;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.container {
  padding: 1rem;
  border-radius: 2rem;
  margin: 0 auto;
  max-width: 60%;
  min-height: 50vh;
  background: url(https://media.istockphoto.com/id/1371224989/vector/blue-and-orange-intersecting-circles-geometric-element-business-circle-vector-illustration.jpg?s=612x612&w=0&k=20&c=YSs6c1U4Til_qqO9lFRKbJf0h8NEO43iCw5SxCUvKsk=);
  background-position: top;
  background-size: cover;
  background-attachment: fixed;
}

#head {
  display: flex;
  justify-content: space-between;
}

#display {
  background: #454847;
  padding: 10px;
  color: #dcd5d5;
  text-align: right;
  height: 30px;
}

#btn-sec button {
  margin: 5px;
  background: url(https://media.istockphoto.com/id/1248542684/vector/abstract-blurred-colorful-background.jpg?s=612x612&w=0&k=20&c=6aJX8oyUBsSBZFQUCJDP7KZ1y4vrf-wEH_SJsuq7B5I=);
  background-position: top;
  background-size: cover;
  background-attachment: fixed;
  padding: 10px;
  border: none;
  box-shadow: 4px 0px 19px red;
  width: 100%;
  color: #ffffff;
transition: .5s ease-in-out  ;
}
#btn-sec button:hover {

  background: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRXbkEuSNOmBQdtN5LgVZLY1IPLjwvjFY8hOdNP-KmF0vfsNNEhPIJ6BPN_ar1mzrBZ4xg&usqp=CAU);
  background-position: top;
  background-size: cover;
  background-attachment: fixed;
}



#btn-sec p {

  display: flex;

}

button {
  background-color: #765978;
  border-radius: 1rem;
  color: #ffffff;
}